  formulaPayrollTax(){
    let income = this.income; /*  income */
    if(this.ruling == true){   /* if 30% ruling */
      income = this.percentage(income, 30);
      if(income>0 && income < 20142){
        let tax = 8.90;

      }
      else if(income >=20142 && income < 33994 ){
       let tax = 13.20;

      }
      else if(income >=33994 && income < 68507){
        let tax = 40.85;

      }
      else if( income >=68507){
        let tax = 51.95;
      }


    }else{
      if(income>0 && income <= 20142){
        let tax = 8.90;   /* division 1 */
        return this.percentageInsurance(income, tax);
      }
      else if(income >20142 && income < 33994 ){
       let tax = 13.20; /* division 2 */
       return this.percentageInsurance(income, tax);

      }
      else if(income >=33994 && income < 68507){
        let tax = 40.85; /* division 3 */
        return this.percentage(income, tax);

      }
      else if( income >=68507){
        let tax = 51.95; /* division 4 */
        return this.percentage(income, tax);

      }


    }

  }


